<template>
  <div class="Detail">
    <first-tab class="firsttab" @firsttabclick="firsttabclick" ref="firsttab"></first-tab>
    <brtter-scroll
      class="wraper"
      ref="scroll"
      :probeType="3"
      :click="true"
      :pullUpLoad="true"
      @scroll="scrollposition"
    >
      <!-- 轮播图 -->
      <detail-swiper
        class="detail-swiper"
        :banners="datailImg"
        ref="dswiper"
      ></detail-swiper>
      <!-- info信息 -->
      <detail-base-info :goods="goods"></detail-base-info>
      <!-- shop信息 -->
      <detail-shop-info :shop="shop"></detail-shop-info>
      <!-- goods信息 -->
      <detail-goods-info
        ref="dgoodsimage"
        class="detail-goods-info"
        :detailInfo="goodsimage"
        @imageLoad="imageLoad"
      ></detail-goods-info>
      <!-- 尺码信息 -->
      <detail-param-info
        ref="param"
        :param-info="paramInfo"
        class="param"
      ></detail-param-info>
      <!-- 评论信息 -->
      <detail-comment-info
        class="detail-comment-info"
        ref="dcomment"
        :commentInfo="commentInfo"
      ></detail-comment-info>
      <!-- 推荐商品信息 -->
      <muchdata
        ref="dgoodsList"
        :muchdataarr="goodsList"
        class="muchdata"
      ></muchdata>
    </brtter-scroll>
    <detail-bottom-bar @addToCart="addToCart"></detail-bottom-bar>
    <return-top
      @click.native="detailreturntopclick"
      class="detailreturntop"
      v-show="detailreturntopshow"
    ></return-top>
  </div>
</template>
<script>
import brtterScroll from "../../components/common/scroll/brtterScroll.vue";

import firstTab from "./childcomponent/firstTab.vue";
import DetailSwiper from "./childcomponent/DetailSwiper.vue";
import DetailBaseInfo from "./childcomponent/DetailBaseInfo.vue";
import DetailShopInfo from "./childcomponent/DetailShopInfo.vue";
import DetailGoodsInfo from "./childcomponent/DetailGoodsInfo.vue";
import DetailCommentInfo from "./childcomponent/DetailCommentInfo.vue";
import DetailBottomBar from "./childcomponent/DetailBottomBar.vue";

import DetailParamInfo from "./childcomponent/DetailParamInfo.vue";
import muchdata from "@/components/common/muchdate/muchdata.vue";
import ReturnTop from "../../components/common/scroll/returnTop.vue";


import { detaildata, Goods, Shop, getRecommend, GoodsParam } from "../../network/detail.js";
import debounce from "@/common/utils.js";

export default {
  name: "Detail",
  data() {
    return {
      iid: null,
      datailImg: [],
      goods: {},
      shop: {},
      goodsimage: {},
      goodsList: [],
      commentInfo: {},
      firsttabclickArr: [],
      tabOffsetTop: "",
      detailreturntopshow: false,
      themeTops: [],
      paramInfo: {},
    };
  },
  components: {
    firstTab,
    DetailSwiper,
    DetailBaseInfo,
    DetailShopInfo,
    brtterScroll,
    DetailGoodsInfo,
    DetailCommentInfo,
    muchdata,
    ReturnTop,
    DetailParamInfo,
    DetailBottomBar
  },

  created() {
    (this.iid = this.$route.query.iid),
      detaildata(this.iid).then((res) => {
        this.datailImg.push(...res.result.itemInfo.topImages);
        const data = res.result;
        //  获取商品信息
        this.goods = new Goods(
          data.itemInfo,
          data.columns,
          data.shopInfo.services
        );
        // 获取店铺信息
        this.shop = new Shop(data.shopInfo);
        //尺码信息
        this.paramInfo = new GoodsParam(
          data.itemParams.info,
          data.itemParams.rule
        );

        //获取详情信息
        this.goodsimage = data.detailInfo;
        // 保存评论数据
        if (data.rate.list) {
          this.commentInfo = data.rate.list[0];
        }
      });
    getRecommend().then((res) => {
      this.goodsList = res.data.list;
    });
  },
  methods: {
    firsttabclick(index) {
      let a = document.querySelector(".detail-swiper");
      let b = document.querySelector(".param");
      let c = document.querySelector(".detail-comment-info");
      let d = document.querySelector(".muchdata");
      switch (index) {
        case 0:
          this.$refs.scroll.scroll.scrollToElement(a, 1000, 0, 0);
          break;
        case 1:
          this.$refs.scroll.scroll.scrollToElement(b, 1000, 0, -40);
          break;
        case 2:
          this.$refs.scroll.scroll.scrollToElement(c, 1000, 0, -40);
          break;
        case 3:
          this.$refs.scroll.scroll.scrollToElement(d, 1000, 0, -40);
          break;
        default:
          break;
      }
    },
    imageLoad() {
      this.$refs.scroll.refresh();

      this.themeTops = [];
      this.themeTops.push(0);
      this.themeTops.push(this.$refs.param.$el.offsetTop);
      this.themeTops.push(this.$refs.dcomment.$el.offsetTop);
      this.themeTops.push(this.$refs.dgoodsList.$el.offsetTop);
      console.log(this.themeTops);
    },
    detailreturntopclick() {
      this.$refs.scroll.scroll.scrollTo(0, 0, 500);
    },
    scrollposition(position) {
      this.detailreturntopshow = -position.y > 1000;
        
        //屏幕滑动，对应的标题切换
      let o = -position.y;

      for (let i = 0; i < this.themeTops.length; i++) {
        if ( (this.$refs.firsttab.topchange !=i) &&((o > this.themeTops[i] && o < this.themeTops[i + 1] )|| (i==3 && o > this.themeTops[i]) )) {
          console.log(i);
          this.$refs.firsttab.topchange = i
        }
      }
    },
    addToCart(){
      //获取发送到购物车的信息
      const product = {}
      product.image=this.datailImg[0]
      product.title=this.goods.title
      //描述信息
      product.desc=this.goods.desc
      product.price=this.goods.realPrice
      product.iid=this.iid
      product.sunCount=1

     
      //store方法
      this.$store.dispatch('cartData' ,product)
    }
  },
};
</script>

<style scoped>
.Detail {
  /* position: relative; */
  position: absolute;
  background: rgb(255, 255, 255);
  z-index: 999;
  height: 100%;
}
.firsttab {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99;
}
.detail-swiper {
  padding-top: 44px;
}
.wraper {
  height: calc(100vh);
}
</style>