<template>
  <div class="firstTab">
    <!-- <tabcontrol :list='["商品","参数","评论","推荐"]' 
   @tabClick="tabClick"
   ></tabcontrol> -->
    <top-fu class="top-nav">
      <img slot="left" src="@/assets/img/common/back.svg" @click="returnup" />
      <div slot="center" class="top-navitem">
        <span
          v-for="(item, index) in title"
          :key="item"
          @click="firsttabclick(index)"
         :class="{toptabclass:topchange === index}">
          {{ item }}
        </span>
      </div>
    </top-fu>
  </div>
</template>

<script>
// import tabcontrol from "@/components/common/tabcontrol.vue";
import topFu from "@/components/top/topfu.vue";


export default {
  data() {
    return {
      title: ["商品", "参数", "评论", "推荐"],
      topchange: 0,
    };
  },
  // props: {
  //   topchange: {
  //     type: Number,
  //     defult: 0,
  //   },
  // },
  components: {
    topFu,
  },
  methods: {
    firsttabclick(index) {
      this.topchange=index;
      this.$emit("firsttabclick", index);
    },
    returnup(){
      this.$router.go(-1)
    }
  },
};
</script>

<style>
.top-nav {
  /* display: flex; */
  background-color: rgb(57, 228, 199);
  /* z-index: 3; */
}
.left {
  width: 60px;
}
.left img {
  padding-top: 12px;
}
.right {
  width: 60px;
}
.center {
  display: flex;
  height: 44px;
  text-align: center;
  line-height: 44px;
  /* padding: 0 20px; */
}
.top-navitem {
  flex: 1;
  font-size: 16px;
}
.top-navitem span{
  padding: 0 8px;
}
.toptabclass {
  color: var(--color-high-text);
}
</style>